.menunavbar {
    overflow: hidden;
    background-color: #333;
    font-family: Arial;
}


/* Links inside the navbar */

.menunavbar a {
    float: left;
    font-size: 16px;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}


/* The dropdown container */

.menudropdown h6 {
    font-size: 12px;
}


/* Dropdown button */

.menudropdown .dropbtn {
    font-size: 12px;
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font: inherit;
    /* Important for vertical align on mobile phones */
    margin: 0;
    /* Important for vertical align on mobile phones */
}


/* Add a red background color to navbar links on hover */

.menunavbar a:hover,
.menudropdown:hover .menudropbtn {
    background-color: rgb(235, 245, 233);
    text-decoration: none;
}


/* Dropdown content (hidden by default) */

.menudropdown-content {
    display: none;
}


/* Mega Menu header, if needed */

.menudropdown-content h5 {
    padding-bottom: -0.5rem;
    /* color: black; */
    color: white;
    font-size: medium;
}


/* Show the dropdown menu on hover */

.menudropdown:hover .menudropdown-content {
    z-index: 5;
    display: list-item;
    width: 95%;
    /* background-color: rgb(211, 207, 207); */
    background-color: rgb(82, 104, 84);
    bottom:5%;
    height: 85%;
    border-radius: 20px;
    border: 1px;
}


/* Style links inside the columns */

.menudropdown-content .dropdownLink {
    /* color: darkolivegreen; */
    color: white;
    font-size: 12px;
}


/* Add a background color on hover */

.link-container a {
