/* GLOBAL STYLES
-------------------------------------------------- */


/* Padding below the footer and lighter body text */

body {
    padding-top: 5rem;
    padding-bottom: 1rem;
    color: #000000;
    font-size: 18px;
}

a:hover {
    text-decoration: none;
}


/*
/* CUSTOMIZE THE CAROUSEL
  -------------------------------------------------- */


/* Carousel base class */

.carousel {
    margin-bottom: 4rem;
}


/* Since positioning the image, we need to help out the caption */

.carousel-caption1 {
    bottom: 3rem;
    margin-top: 18rem;
    color: rgb(187, 70, 70);
    /* z-index: 5; */
}


/* Declare heights because of positioning of img element */

.carousel-item {
    height: 70vw;
}

.carousel-item>img {
    position: absolute;
    top: 0;
    left: 0;
    /* min-width: 100%; */
    height: 70vw;
}


/* MARKETING CONTENT
  -------------------------------------------------- */


/* Center align the text within the three columns below the carousel */

.marketing .col-lg-4 {
    margin-bottom: 1.5rem;
    text-align: center;
}

.marketing h2 {
    font-weight: 400;
}

.marketing .col-lg-4 p {
    margin-right: .75rem;
    margin-left: .75rem;
}


/* Featurettes
  ------------------------- */

.featurette-divider {
    margin: 3.5rem 0rem;
    /* border-top: 1px solid;
    color: grey; */
    /* Space out the Bootstrap <hr> more */
}

.fa {
    text-align: center;
    text-decoration: none;
}

.fa:hover {
    color: honeydew;
}


/* Thin out the marketing headings */

.featurette-heading {
    font-weight: 300;
    line-height: 1;
    letter-spacing: -.05rem;
}


/* RESPONSIVE CSS
  -------------------------------------------------- */

@media (min-width: 40em) {
    .carousel-caption p {
        margin-bottom: 1.25rem;
        font-size: 1.25rem;
        line-height: 1.4;
    }
    .featurette-heading {
        font-size: 50px;
    }
}

